import React from 'react'
import classes from './App.module.css'
import axios from './axios/axios-bot'

class App extends React.Component {

  state = {
    message: '',
    incomingMessage: '',
    fromName: '',
    fromLastName: '',
    replyMessage: '',
    fromIdMessage: ''
  }

  sendMessage = (e) => {
    e.preventDefault()

    const message = this.state.message.trim()
    const url = 'https://api.telegram.org/bot1078504713:AAF_98VqMImTPtGr1A_4de55foz0XZIPYT8/sendMessage?chat_id=-445137145&text='

    if (message !== '') {
      let xhttp = new XMLHttpRequest()
      xhttp.open('GET', url+message, true)
      xhttp.send()
    }

    this.setState({message: ''})
  }

  sendReplyMessage = (e) => {
    e.preventDefault()

    const message = this.state.replyMessage.trim()
    const id = this.state.fromIdMessage
    const url = `https://api.telegram.org/bot1078504713:AAF_98VqMImTPtGr1A_4de55foz0XZIPYT8/sendMessage?chat_id=-445137145&text=${message}&reply_to_message_id=${id}`

    console.log("URL", url)

    if (message !== '') {
      let xhttp = new XMLHttpRequest()
      xhttp.open('GET', url, true)
      xhttp.send()
    }

    this.setState({replyMessage: ''})
  }

  inputMessage = (e) => {
    let message = e.target.value
    this.setState({message})
  }

  inputReplyMessage = (e) => {
    let replyMessage = e.target.value
    this.setState({replyMessage})
  }

  getUpdates = async () => {
    try {
      const response = await axios.get('/getUpdates')
      const result = response.data.result[response.data.result.length -1]

      console.log('result', result)

      const fromName = result.message.from.first_name
      const fromLastName = result.message.from.last_name
      const fromIdMessage = result.message.message_id
      const incomingMessage = result.message.text

      this.setState({
        incomingMessage, fromName, fromLastName, fromIdMessage
      })
    } catch (e) {
      console.log(e)
    }
  }

  componentDidMount() {
    this.getUpdates()
    setInterval(this.getUpdates, 3000)
  }

  render() {
    const cls_btn = ['btn', 'btn-default', classes.Btn]
    const cls_container = [classes.Container]
    const fromUser = this.state.fromName + ' '+ this.state.fromLastName
    return (
      <div className={classes.App}>
        <div className={cls_container.join(' ')} style={{marginBottom: 50}}>
          <h2 className={classes.Color}>Правду і нічого крім правди:</h2>
          <form>
            <div className="form-group">
              <input type="text" className="form-control" id="message-text" placeholder="Вали гезде" name="message-text" value={this.state.message} onChange={this.inputMessage} />
            </div>
            <button type="submit" onClick={this.sendMessage} className={cls_btn.join(' ')}>Чпок</button>
          </form>
        </div>

        <div className={cls_container.join(' ')}>
          <h2 className={classes.Color}>Останнє вхідне повідомлення:</h2>
          <div className={classes.Color}>
            Від: <strong>{fromUser}</strong> - "{this.state.incomingMessage}"
          </div>
          <form>
            <div className="form-group">
              <input type="text" className="form-control" id="message-text" placeholder={"Відповідь для "+fromUser} name="message-text" value={this.state.replyMessage} onChange={this.inputReplyMessage} />
            </div>
            <button type="submit" onClick={this.sendReplyMessage} className={cls_btn.join(' ')}>Уяк</button>
          </form>
        </div>
      </div>
    );
  }
}

export default App;
